/* General */

body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

/* Main */

.main {
    background-color: rgb(75, 0, 133);
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    max-width: 60vw;
}

/* Box */

.box {
    background-color: white;
    padding: 20px 30px;
    display: flex;
    border-radius: 6px;
}

.title {
    margin: 0;
    padding: 20px 50px 0 0;
    font-size: 30px;
    font-weight: 500;
    flex: 30%
}

/* Collapsable Accordion */

.accordion-box {
    flex: 70%;
}

/* Question Box  */

.question-box {
    margin: 20px 0;
    border: 2px solid lightgray;
    border-radius: 8px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.question-box .top {
    display: flex;
    align-items: center;
}

.question-box .question {
    flex: 85%;
    margin: 24px;
    font-size: 18px;
    font-weight: 600;
}

.question-box .icon {
    flex: 15%;
    font-size: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.question-box .icon:active {
    font-size: 28px;
}


.question-box .answer {
    margin: 18px 24px;
}